import CommonActionsHelper from '../components/CommonActionsHelper';
import SelectGroupHelper from '../components/SelectGroupHelper';
import ComponentStateHelper from './ComponentStateHelper';
import IndexPathHelper from './IndexPathHelper';
import PathTranslationHelper from './PathTranslationHelper';
import UserDefPathHelper from './UserDefPathHelper';

/**
 * Helper methods to create trace log entries.
*/
export default class TraceLogHelper {

  /**
   * Dump the state stored in all relevant state managers (ComponentStateManager, PresenterStateManager, IncidentsAccumulator etc.) to the trace log.
   * 
   * @param {*} runtime The common runtime context structure.
   */
  static dumpSnapshotToTrace(runtime) {
    const {
      taskManager,
      presenterStateManager,
      incidentsAccumulator,
      clipboardManager,
      statemachinesManager,
      calculatorsManager,
      variableManager
    } = runtime;
    const { test, item, task } = taskManager.getCurrentTestTaskItemNames();
    const currentStatePathRoot = taskManager.getCurrentStatePathRoot();

    const now = new Date();
    runtime.traceLogBuffer.reportEvent('Snapshot', now, {
      components: ComponentStateHelper.buildComponentsSnapshot(test, item, task, runtime),
      presentation: presenterStateManager.getTaskState(currentStatePathRoot),
      incidents: incidentsAccumulator.getAllValuesForTask(currentStatePathRoot, now.getTime()),
      clipboard: clipboardManager.getStateForTracing(),
      statemachine: {
        current: {
          states: statemachinesManager.getCurrentStateMachineLeafStates()
        },
        history: statemachinesManager.getHistoryStatemachineData()
      },
      calculator: calculatorsManager.getStateForTask(currentStatePathRoot),
      variables: variableManager.buildVariablesInfoArrayForTask(currentStatePathRoot)
    });

    // return the time of the dump for testing purposes:
    return now;
  }

  /**
   * Dump our full state (i.e. all state from all our state memories) wrapped in trace log entry 
   * to the trace log. 
   */
  static dumpFullStateToTrace(runtime) {
    const { taskManager, traceLogBuffer } = runtime;
    traceLogBuffer.reportEvent('FullState', new Date(), taskManager.getAllTasksState());
  }

  /**
   * Dump a state change to the trace log.
   */
  static dumpStateChangeToTrace(stateType, stateDelta, traceLogBuffer) {
    traceLogBuffer.reportEvent('StateChange', new Date(), {
      stateType,
      stateDelta
    });
  }

  /**
   * Dump a render call to the trace log.
   */
  static dumpRenderTriggeredToTrace(indexPath, traceLogBuffer, replayStepMonitor) {
    traceLogBuffer.reportEvent('RenderTriggered', new Date(), {
      indexPath,
      replayStepId: replayStepMonitor.getCurrentReplayStepId()
    });
  }

  /**
   * Dump a context menu open/close call to the trace log.
   */
  static dumpContextMenuTriggeredToTrace(menuConfig, shouldOpen, traceLogBuffer, replayStepMonitor) {
    const entries = shouldOpen ? menuConfig.entries : undefined;
    const event = shouldOpen ? {
      clientX: menuConfig.event.clientX,
      clientY: menuConfig.event.clientY,
      type: menuConfig.event.type
    } : undefined;

    traceLogBuffer.reportEvent('ContextMenu', new Date(), {
      event,
      entries,
      shouldOpen,
      replayStepId: replayStepMonitor.getCurrentReplayStepId()
    });
  }

  /**
   * Dump a scroll triggered call to the tracelog
   * @param {*} path 
   * @param {*} scrollLeft 
   * @param {*} scrollTop 
   * @param {*} traceLogBuffer 
   */
  static dumpScrollTriggeredToTrace(path, scrollLeft, scrollTop, traceLogBuffer, replayStepMonitor) {
    traceLogBuffer.reportEvent('ScrollTriggered', new Date(), {
      path,
      scrollLeft,
      scrollTop,
      replayStepId: replayStepMonitor.getCurrentReplayStepId()
    });
  }

  /**
   * Wrapper function for throttling/debouncing 
   * 
   * 
   * @returns {Function(currentScrollValue, currentOrientationValue, path, runtime)}
   */
  static traceScrollWrap() {
    return (currentScrollValue, currentOrientationValue, path, runtime) => {
      TraceLogHelper.traceScroll(path, runtime, currentScrollValue, currentOrientationValue);
    }
  }

  /**
   * Traces the scroll interaction
   * 
   * @param {string} path The Index Path of the dislplay component instance.
   * @param {*} runtime The common runtime context structure.
   * @param {*} currentScrollValue The current scroll values of the component
   * @param {*} currentOrientationValue The current orientation of the scrolling component
   * 
   */
  static traceScroll(path, runtime, currentScrollValue, currentOrientationValue) {
    const scrollTraceDetails = TraceLogHelper.buildScrollTraceDetails(currentScrollValue, currentOrientationValue);
    if (scrollTraceDetails) {
      CommonActionsHelper.traceUserInteraction("ScrollbarMove", path, scrollTraceDetails, undefined, undefined, runtime);
      runtime.replayStepMonitor.incrementReplayStep();
      TraceLogHelper.dumpScrollTriggeredToTrace(path, currentScrollValue.scrollLeft, currentScrollValue.scrollTop, runtime.traceLogBuffer, runtime.replayStepMonitor);
    }
  }

  /**
   * Helper function that builds the trace details needed for scrolling
   * 
   * @param {*} currentScrollValue The current scroll values of the component
   * @param {*} currentOrientationValue The current orientation of the scrolling component
   * 
   * @returns {Object} the trace log details
   */
  static buildScrollTraceDetails(currentScrollValue, currentOrientationValue) {
    const { horizontal, vertical } = TraceLogHelper.computeScrollPosition(currentScrollValue);
    const traceDetails = {
      orientation: currentOrientationValue.orientation,
      direction: currentOrientationValue.direction
    }

    if (horizontal || horizontal === 0) {
      traceDetails.horizontalScroll = horizontal;
    }

    if (vertical || vertical === 0) {
      traceDetails.verticalScroll = vertical;
    }

    if (currentOrientationValue.orientation || currentOrientationValue.direction) {
      return traceDetails;
    }

    return null;
  }

  /**
   * Helper function that computs the scroll orientation
   * 
   * @param {*} currentScrollValue The current scroll values of the component
   * @param {*} lastScrollValue The last known scroll values of the component
   * 
   * @returns {Object} the scroll orientation
   */
  static computeScrollOrientation(currentScrollValue, lastScrollValue) {
    if (!currentScrollValue || !lastScrollValue) {
      return {
        direction: null,
        orientation: null
      };
    }

    const isVerticalOrientation = currentScrollValue.scrollTop !== lastScrollValue.scrollTop;
    const orientation = isVerticalOrientation ? "vertical" : "horizontal";
    let direction;

    if (isVerticalOrientation) {
      direction = currentScrollValue.scrollTop < lastScrollValue.scrollTop ? "up" : "down";
    } else {
      direction = currentScrollValue.scrollLeft < lastScrollValue.scrollLeft ? "left" : "right";
    }

    return {
      direction,
      orientation
    }
  }

  /**
   * Helper function that computs the scroll position
   * 
   * @param {*} currentScrollValue The current scroll values of the component
   * @param {*} lastScrollValue The last known scroll values of the component
   * 
   * @returns {Object} the scroll position
   */
  static computeScrollPosition(currentScrollValue) {
    const { scrollTop, scrollLeft, scrollHeight, scrollWidth, clientHeight, clientWidth } = currentScrollValue;

    return {
      vertical: Math.round(100 * (scrollTop / (scrollHeight - clientHeight))),
      horizontal: Math.round(100 * (scrollLeft / (scrollWidth - clientWidth))),
    }
  }

  /**
   * Helper function that gets scroll information from a browser event
   * 
   * @param {*} event The browser event
   * 
   * @returns {Object} the scroll data
   */
  static getScrollDataFromEvent(event) {
    const { scrollTop, scrollLeft, scrollHeight, scrollWidth, clientHeight, clientWidth } = event.target;

    return {
      scrollTop,
      scrollLeft,
      scrollHeight,
      scrollWidth,
      clientHeight,
      clientWidth
    }
  }

  /**
   * Helper function that retrieves useful trace information before the setActive operator is applied.
   * 
   * @param {string} absoluteUserDefId The UDI of the component instance.
   * @param {*} runtime The common runtime context structure.
   * 
   * @returns {Object} 
   *    oldSelectedValue: if the component is in a selected state
   *    oldComboSelectedId: the current combo selection
   */
  static getTraceDataBeforeSetSelection(absoluteUserDefId, runtime) {
    const indexPath = PathTranslationHelper.getIndexPathForUserDefPath(absoluteUserDefId, runtime);
    const pathState = runtime.componentStateManager.findOrBuildStateForPathId(indexPath, runtime);
    const componentType = ComponentStateHelper.getComponentTypeForIndexPath(indexPath, runtime);
    return {
      oldSelectedValue: SelectGroupHelper.extractSelectedState(pathState, indexPath, runtime),
      oldComboSelectedId:
        (componentType === 'ComboBoxItem') ? SelectGroupHelper.getComboboxSelectedItem(IndexPathHelper.dropIndexFromPageSegment(indexPath), runtime) : ''
    }
  }

  /**
   * Helper function that generates state change specific component traces for setActive operator.
   * 
   * @param {string} absoluteUserDefId The UDI of the component instance.
   * @param {*} runtime The common runtime context structure.
   * @param {Object} 
   *    oldSelectedValue: if the component is in a selected state
   *    oldComboSelectedId: the current combo selection
   */
  static traceChangesAfterSetSelection(absoluteUserDefId, runtime, dataBeforeSelection) {
    const { oldSelectedValue } = dataBeforeSelection;
    let indexPath = PathTranslationHelper.getIndexPathForUserDefPath(absoluteUserDefId, runtime);
    const pathState = runtime.componentStateManager.findOrBuildStateForPathId(indexPath, runtime);
    const newSelectedValue = SelectGroupHelper.extractSelectedState(pathState, indexPath, runtime);

    // state change
    if (oldSelectedValue !== newSelectedValue) {
      const { pageConfigurationsManager } = runtime;
      const componentType = ComponentStateHelper.getComponentTypeForIndexPath(indexPath, runtime);
      let { config } = pageConfigurationsManager.findConfigurationForPageSegment(IndexPathHelper.getLastPageSegmentFromPath(indexPath));
      if (componentType === 'ComboBoxItem') {
        indexPath = IndexPathHelper.dropIndexFromPageSegment(indexPath);
        ({ config } = pageConfigurationsManager.findConfigurationForPageSegment(IndexPathHelper.getLastPageSegmentFromPath(indexPath)));
      }

      const traceDetails = TraceLogHelper.buildTraceDetailsStructure(absoluteUserDefId, runtime, dataBeforeSelection);
      if (traceDetails !== undefined) {
        const traceConfig = config.trace;
        if (traceConfig.skipTrace === undefined || traceConfig.skipTrace !== true) {
          const extendedValues = {};
          CommonActionsHelper.extendTraceDetailsObject(extendedValues, traceConfig.addOn);
          CommonActionsHelper.extendTraceDetailsObject(extendedValues, traceDetails);

          const details = CommonActionsHelper.buildTraceLogDetails(indexPath, extendedValues, undefined, runtime);
          const timestamp = new Date();
          runtime.traceLogBuffer.reportEvent(traceConfig.type, timestamp, details);
        }
      }
    }
  }

  /**
   * Helper function that generates specific component trace details for setActive operator.
   * 
   * @param {string} absoluteUserDefId The UDI of the component instance.
   * @param {*} runtime The common runtime context structure.
   * @param {Object} 
   *    oldSelectedValue: if the component is in a selected state
   *    oldComboSelectedId: the current combo selection
   */
  static buildTraceDetailsStructure(absoluteUserDefId, runtime, dataBeforeSelection) {
    const { oldSelectedValue, oldComboSelectedId } = dataBeforeSelection;
    const { pageConfigurationsManager } = runtime;
    const indexPath = PathTranslationHelper.getIndexPathForUserDefPath(absoluteUserDefId, runtime);
    const componentType = ComponentStateHelper.getComponentTypeForIndexPath(indexPath, runtime);
    switch (componentType) {
      case 'CbaCheckbox':
      case 'CbaRadioButton':
      case 'CbaPolygon':
      case 'CbaRichTextField':
      case 'CbaLink':
        return CommonActionsHelper.buildOldSelectedTraceLogValueObject(oldSelectedValue);
      case 'CbaButton': {
        const traceDetails = CommonActionsHelper.buildOldSelectedTraceLogValueObject(oldSelectedValue);
        const { config } = pageConfigurationsManager.findConfigurationForPageSegment(IndexPathHelper.getLastPageSegmentFromPath(indexPath));
        if (config.subtype !== undefined) {
          traceDetails.subtype = config.subtype;
        }
        return traceDetails;
      }
      case 'ComboBoxItem': {
        const oldSelectedIndex = SelectGroupHelper.getComboboxItemIndexByUdi(IndexPathHelper.dropIndexFromPageSegment(indexPath), oldComboSelectedId, runtime);
        const newSelectedId = SelectGroupHelper.getComboboxSelectedItem(IndexPathHelper.dropIndexFromPageSegment(indexPath), runtime);
        const newSelectedIndex = SelectGroupHelper.getComboboxItemIndexByUdi(IndexPathHelper.dropIndexFromPageSegment(indexPath), newSelectedId, runtime);
        return {
          oldSelected: oldSelectedIndex,
          oldSelectedUserDefId: oldComboSelectedId,
          newSelected: newSelectedIndex,
          newSelectedUserDefId: newSelectedId
        }
      }
      case 'CbaTableCell': {
        const tableIndexPath = IndexPathHelper.dropIndexFromPageSegment(indexPath);
        const { row, column } = pageConfigurationsManager.findConfigurationForPageSegment(IndexPathHelper.getLastPageSegmentFromPath(indexPath)).config;
        const tableUserDefIdPath = PathTranslationHelper.getUserDefPathForIndexPath(tableIndexPath, runtime);
        return {
          tableUserDefIdPath,
          tableUserDefId: UserDefPathHelper.getLastUserDefIdFromPath(tableUserDefIdPath),
          row,
          column,
          oldSelectedValue
        }
      }
      default:
        return undefined;
    }
  }

}
