import React, { Component } from 'react';
import PropTypes from 'prop-types';
import shortid from 'shortid';
import PropTypesHelper from './PropTypesHelper';
import CommonActionsHelper from './CommonActionsHelper';
import CommonConfigHelper from '../config/CommonConfigHelper';
import StateAttributeAccess from '../state/StateAttributeAccess';
import ComponentStateHelper from '../state/ComponentStateHelper';
import SelectGroupHelper from './SelectGroupHelper';
import RenderingHelper from './RenderingHelper';
import './CbaRichTextField/css/repeatable.css';


const DEFAULT_LABEL_POSITION = 10;

/**
 * A display component that displays a labeled check box.
 */
export default class CbaCheckbox extends Component {

  constructor(props) {
    super(props);

    this.onClickHandler = this.onClickHandler.bind(this);
    this.updateStateAndTriggerRendering = this.updateStateAndTriggerRendering.bind(this);
  }

  componentDidMount() {
    RenderingHelper.onMount(this);
  }

  componentWillUnmount() {
    RenderingHelper.onUnmount(this);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    RenderingHelper.onReceiveProps(this, nextProps);
  }

  onClickHandler(event) {
    const { config, runtime, path } = this.props;
    const pathState = ComponentStateHelper.getState(this);
    const selectedState = SelectGroupHelper.extractSelectedState(pathState, path, runtime);

    CommonActionsHelper.doBasicOnClick(event, path, runtime);
    CommonActionsHelper.traceUserInteractionPerConfig(config, path,
      CommonActionsHelper.buildOldSelectedTraceLogValueObject(selectedState),
      event, runtime);

    // Flip state: 'true' <-> 'false'
    this.updateStateAndTriggerRendering(pathState);

    SelectGroupHelper.doPageSwitchOrLetTheContainerDoIt(this, pathState);

    // send statemachine event depending on old selected state:
    CommonActionsHelper.sendStandardOrAlternateEvent(selectedState, config, runtime);
  }

  onContextMenuHandler = (event) => {
    CommonActionsHelper.doContextMenuOpen(this, event);
  }

  updateStateAndTriggerRendering(pathState) {
    const { runtime, path } = this.props;
    const oldSelected = SelectGroupHelper.extractSelectedState(pathState, path, runtime);

    StateAttributeAccess.setVisited(pathState, true);
    SelectGroupHelper.setSelectedForPossiblyControlledComponent(!oldSelected, path, pathState, true, false, runtime);
  }

  onClickFrozenHandler = (event) => {
    const { runtime, path } = this.props;
    CommonActionsHelper.doStandardOnFrozenInteraction(path, runtime, 'CheckBox');
    event.stopPropagation();
  }

  addFlexAlignmentToStyles = (styleObject, verticalAlign, horizontalAlign) => Object.assign(styleObject, {
    display: "flex",
    flexDirection: "column",
    justifyContent: this.computeFlexAlignment(verticalAlign),
    alignItems: this.computeFlexAlignment(horizontalAlign)
  });

  computeFlexAlignment = (alignment) => {
    let flexAlignment = "";
    switch (alignment) {
      case "top": flexAlignment = "flex-start"; break;
      case "left": flexAlignment = "flex-start"; break;
      case "center": flexAlignment = "center"; break;
      case "bottom": flexAlignment = "flex-end"; break;
      case "right": flexAlignment = "flex-end"; break;
      default: flexAlignment = "center"; break;
    }
    return flexAlignment;
  }

  computeMargins = (verticalAlign, isTop) => {
    let marginValue = "auto";
    if (verticalAlign === 'bottom' && !isTop) marginValue = 2.5;
    if (verticalAlign === 'top' && isTop) marginValue = 2.5;
    return marginValue;
  }

  computeControlIconPosition = (hasLabel, horizontalAlign, controlIconSize, width) => {
    let controlPosition = {};
    if (!hasLabel) {
      if (horizontalAlign === "center") {
        controlPosition = Object.assign(controlPosition, {
          left: (width - controlIconSize) / 2,
          right: 'auto'
        });
      } else if (horizontalAlign === "left") {
        controlPosition = Object.assign(controlPosition, {
          left: 2.5,
          right: 'auto'
        })
      } else if (horizontalAlign === "right") {
        controlPosition = Object.assign(controlPosition, {
          left: 'auto',
          right: 2.5
        })
      }
    }
    return controlPosition;
  }

  render() {
    const { config, runtime, path, orientation } = this.props;
    const pathState = ComponentStateHelper.getState(this);
    const selectedState = SelectGroupHelper.extractSelectedState(pathState, path, runtime);
    const text = RenderingHelper.extractMultilineText(CommonConfigHelper.buildDisplayText(config, pathState, runtime));
    const { label, textWrap, controlIconSize, horizontalAlign, verticalAlign, text: checkboxLabel, position } = config;
    const { distance, position: labelPosition } = label;
    const { width } = position;
    const leftToRight = (labelPosition === 'right');

    let wrapperStyle = CommonConfigHelper.buildStyleByIndexPath(path, config, selectedState, orientation, runtime);
    wrapperStyle = this.addFlexAlignmentToStyles(wrapperStyle, verticalAlign, horizontalAlign);

    const textWrapStyle = {
      whiteSpace: textWrap ? "initial" : "nowrap",
      marginLeft: leftToRight ? 20 : 0,
      marginRight: leftToRight ? 0 : 20,
      marginBottom: this.computeMargins(verticalAlign, false),
      marginTop: this.computeMargins(verticalAlign, true),
      justifyContent: this.computeFlexAlignment(horizontalAlign),
    }

    const inputStyle = {
      left: (leftToRight) ? 2.5 : "auto",
      right: (leftToRight) ? "auto" : 2.5,
      marginBottom: this.computeMargins(verticalAlign, false),
      marginTop: this.computeMargins(verticalAlign, true),
      cursor: "inherit",
      ...this.computeControlIconPosition(checkboxLabel.label, horizontalAlign, controlIconSize, width)
    }

    const labelPositionDelta = distance - DEFAULT_LABEL_POSITION;
    const textStyle = {
      marginLeft: leftToRight ? labelPositionDelta : 0,
      marginRight: leftToRight ? 0 : labelPositionDelta,
      display: checkboxLabel.label ? "inline" : "none"
    }

    const labelStyle = {
      cursor: "inherit"
    }

    const checkBoxWrapStyle = {
      ...((checkboxLabel.label) ? {
        transform: 'translateX(0)'
      } : {})
    }

    const forKey = shortid.generate();

    // TODO: This class resembles CbaRadioButton pretty much 
    //       -> Merge into one class once we know that we keep implementation of both as <input> plus <div>.
    return (
      <React.Fragment>
        <div
          style={wrapperStyle}
          data-cba-id={config.pageEditId}
          title={CommonConfigHelper.buildTitle(config)}
          onClick={this.onClickHandler}
          onContextMenu={this.onContextMenuHandler}
          className={CommonConfigHelper.getCssClassNamesValue(config, "inputWrapper overflow-hidden")}
        >
          <div className="checkBoxWrap" style={checkBoxWrapStyle}>
            <label
              style={labelStyle}
              htmlFor={forKey}
              className="label"
              onClick={(event) => { event.stopPropagation(); }}
            >
              <span style={textWrapStyle}>
                <span style={textStyle} className="inputTextWrap">{text}</span>
              </span>
            </label>
            <input
              className={`radioCheckBoxSize${controlIconSize || 13}`}
              style={inputStyle}
              id={forKey}
              type="checkbox"
              checked={selectedState}
              disabled={StateAttributeAccess.extractDisabled(pathState)}
              // using a blank function to remove the warning src: https://stackoverflow.com/questions/36715901/reactjs-error-warning
              onChange={() => { }}
              tabIndex={config.tabIndex}
            />
          </div>
        </div>
        {StateAttributeAccess.extractDisabled(pathState) && <div className="CbaCheckbox-mask" onClick={e => this.onClickFrozenHandler(e)} onContextMenu={e => e.preventDefault()} style={CommonConfigHelper.buildStyleForMask(wrapperStyle)} />}
      </React.Fragment>
    );
  }

}

CbaCheckbox.propTypes = {
  runtime: PropTypes.shape(PropTypesHelper.getStandardRuntimePropTypes()).isRequired,
  path: PropTypes.string.isRequired,
  config: PropTypes.shape(
    PropTypesHelper.addPropTypes(
      PropTypesHelper.addSelectGroupMemberConfigPropTypes(PropTypesHelper.getStandardConfigPropTypes(false)),
      {
        textWrap: PropTypes.bool.isRequired,
        label: PropTypesHelper.getInputLabelConfig(),
        controlIconSize: PropTypes.number.isRequired,
        horizontalAlign: PropTypes.string.isRequired,
        verticalAlign: PropTypes.string.isRequired
      }
    )
  ).isRequired,
  orientation: PropTypes.string.isRequired
}
